import {Component, OnDestroy, OnInit} from '@angular/core';
import {LoadingIndicatorService} from './providers/loading.service';
import {SharedService} from './shared/services/shared.service';
import {Subscription} from 'rxjs/Subscription';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html'
})

export class AppComponent implements OnInit, OnDestroy {
  title = 'app';
  loading = false;
  private loadingSubscription: Subscription;

  constructor(private sharedService: SharedService, private loadingIndicatorService: LoadingIndicatorService) {

  }

  ngOnInit() {
    this.loadingSubscription = this.loadingIndicatorService.loadingAnnounced$
      .subscribe(async (isLoading: boolean) => this.loading = await isLoading);
  }

  ngOnDestroy() {
    this.loadingSubscription.unsubscribe();
  }

}



