import {Injectable} from '@angular/core';
import {HttpClient, HttpHeaders, HttpParams} from '@angular/common/http';
import {HttpErrorHandler, HandleError} from '../http-error-handler.service';
import {Observable} from 'rxjs/Observable';
import {catchError} from 'rxjs/operators';
import {CookieService} from 'ngx-cookie-service';

@Injectable()
export class RolesService {
  rolesURI = 'http://localhost:50487/api/role/';
  private handleError: HandleError;


  constructor(private http: HttpClient, private cookieService: CookieService,
              httpErrorHandler: HttpErrorHandler) {
    this.handleError = httpErrorHandler.createHandleError('RolesService');
  }


  getById(id: string): Observable<any> {

    const httpOptions = {
      headers: new HttpHeaders({
        'Content-Type': 'application/json'
      }),
      params: new HttpParams().set('id', id)
    };

    return this.http.get(this.rolesURI + 'GetRolesById', httpOptions)
      .pipe(
        catchError(this.handleError('getById', {error: true}))
      );
  }

  get(): Observable<any> {

    const httpOptions = {
      headers: new HttpHeaders({
        'Content-Type': 'application/json'
      })
    };
    return this.http.get(this.rolesURI, httpOptions)
      .pipe(
        catchError(this.handleError('get', {error: true}))
      );
  }

  save(rol: any): Observable<any> {

    const httpOptions = {
      headers: new HttpHeaders({
        'Content-Type': 'application/json'
      })
    };
    return this.http.post(this.rolesURI + 'Create', rol, httpOptions)
      .pipe(
        catchError(this.handleError('save', {error: true}))
      );
  }

  update(role: any): Observable<any> {

    const httpOptions = {
      headers: new HttpHeaders({
        'Content-Type': 'application/json'
      })
    };
    return this.http.put(this.rolesURI, {
      Id: role.Id,
      Name: role.Name,
      Users: role.Users,
      Estatus: role.Estatus,
      Description: role.Description
    }, httpOptions)
      .pipe(
        catchError(this.handleError('save', {error: true}))
      );
  }
}



