import {Injectable} from '@angular/core';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {HttpErrorHandler, HandleError} from '../http-error-handler.service';
import {Observable} from 'rxjs/Observable';
import {catchError} from 'rxjs/operators';
import {CookieService} from 'ngx-cookie-service';

@Injectable()
export class AreaOperativaService {
  rolesURI = 'http://localhost:50487/api/AreaOperativa';
  private handleError: HandleError;


  constructor(private http: HttpClient, private cookieService: CookieService,
              httpErrorHandler: HttpErrorHandler) {
    this.handleError = httpErrorHandler.createHandleError('AreaOperativaService');
  }


  getById(id: string): Observable<any> {

    const httpOptions = {
      headers: new HttpHeaders({
        'Content-Type': 'application/json'
      })
    };
    return this.http.get(this.rolesURI + id, httpOptions)
      .pipe(
        catchError(this.handleError('getById', {error: true}))
      );
  }

  get(): Observable<any> {

    const httpOptions = {
      headers: new HttpHeaders({
        'Content-Type': 'application/json'
      })
    };
    return this.http.get(this.rolesURI, httpOptions)
      .pipe(
        catchError(this.handleError('get', {error: true}))
      );
  }
}



