import {Injectable} from '@angular/core';
import {HttpClient, HttpHeaders, HttpParams} from '@angular/common/http';
import {HttpErrorHandler, HandleError} from '../http-error-handler.service';
import {Observable} from 'rxjs/Observable';
import {catchError} from 'rxjs/operators';
import {CookieService} from 'ngx-cookie-service';

@Injectable()
export class ModulesService {
  rolesURI = 'http://servicemarket-001-site7.gtempurl.com/api/';
  private handleError: HandleError;


  constructor(private http: HttpClient, private cookieService: CookieService,
              httpErrorHandler: HttpErrorHandler) {
    this.handleError = httpErrorHandler.createHandleError('ModulesService');
  }


  get(): Observable<any> {

    const httpOptions = {
      headers: new HttpHeaders({
        'Content-Type': 'application/json'
      })
    };
    return this.http.get(this.rolesURI + 'modulo', httpOptions)
      .pipe(
        catchError(this.handleError('get', {error: true}))
      );
  }

  getByRol(id: string): Observable<any> {

    const httpOptions = {
      headers: new HttpHeaders({
        'Content-Type': 'application/json'
      }),
      params: new HttpParams().set('id', id)
    };
    return this.http.get(this.rolesURI + 'ModuloRol/GetModulosByRoles/', httpOptions)
      .pipe(
        catchError(this.handleError('get', {error: true}))
      );
  }
}



