import {EventEmitter, Injectable} from '@angular/core';
import {HttpRequest} from '@angular/common/http';
import {Subject} from 'rxjs/Subject';

@Injectable()
export class LoadingIndicatorService {

  onLoadingChanged: EventEmitter<boolean> = new EventEmitter<boolean>();

  private loadingAnnounced = new Subject<boolean>();
  loadingAnnounced$ = this.loadingAnnounced.asObservable();
  private requests: HttpRequest<any>[] = [];

  announceLoadingMessage(tickedMessage: boolean) {
    this.loadingAnnounced.next(tickedMessage);
  }

  onStarted(req: HttpRequest<any>): void {
    this.requests.push(req);
    this.notify();
  }

  onFinished(req: HttpRequest<any>): void {
    const index = this.requests.indexOf(req);
    if (index !== -1) {
      this.requests.splice(index, 1);
    }
    this.notify();
  }

  private notify(): void {
    this.announceLoadingMessage(this.requests.length !== 0);
    this.onLoadingChanged.emit(this.requests.length !== 0);
  }

}



