import {Injectable} from '@angular/core';
import {HttpInterceptor, HttpHandler, HttpRequest} from '@angular/common/http';
import {CookieService} from 'ngx-cookie-service';
import {LoadingIndicatorService} from '../providers/loading.service';
import {finalize} from 'rxjs/operators';

@Injectable()
export class AuthInterceptor implements HttpInterceptor {

  constructor(private cookieService: CookieService, private loadingIndicatorService: LoadingIndicatorService) {
  }

  intercept(req: HttpRequest<any>, next: HttpHandler) {
    this.loadingIndicatorService.onStarted(req);


    if (req.url.indexOf('api/oauth/token') > -1) {
      return next.handle(req).pipe(
        finalize(() => this.loadingIndicatorService.onFinished(req))
      );
    } else {

      const authToken = this.cookieService.get('token');
      const authReq = req.clone({setHeaders: {Authorization: 'Bearer ' + authToken}});

      return next.handle(authReq).pipe(
        finalize(() => this.loadingIndicatorService.onFinished(req))
      );
    }
  }
}



