import {Injectable} from '@angular/core';
import {HttpClient, HttpHeaders, HttpParams} from '@angular/common/http';
import {HttpErrorHandler, HandleError} from '../http-error-handler.service';
import {Observable} from 'rxjs/Observable';
import {catchError} from 'rxjs/operators';
import {CookieService} from 'ngx-cookie-service';

@Injectable()
export class BanistmoService {
  banistmoURI = 'http://localhost:50487/';

  private handleError: HandleError;
  public user: any;


  constructor(private http: HttpClient, private cookieService: CookieService,
              httpErrorHandler: HttpErrorHandler) {
    this.handleError = httpErrorHandler.createHandleError('BanistmoService');
    this.user = {};
  }


  setSession(token: string): void {
    this.cookieService.set('token', token);
  }

  getSession(): void {
    this.cookieService.get('token');
  }


  login(username: string, password: string, grant_type: string): Observable<any> {

    const httpOptions = {
      headers: new HttpHeaders({
        'Content-Type': 'application/x-www-form-urlencoded'
      })
    };
    let Params = new HttpParams();

    Params = Params.append('username', username);
    Params = Params.append('password', password);
    Params = Params.append('grant_type', grant_type);

    return this.http.post(this.banistmoURI + 'api/oauth/token', Params, httpOptions)
      .pipe(
        catchError(this.handleError('getHeroes', {error: true}))
      );
  }

  getUserAttributes(): Observable<any> {

    const httpOptions = {
      headers: new HttpHeaders({
        'Content-Type': 'application/x-www-form-urlencoded'

      })
    };

    return this.http.get(this.banistmoURI + 'api/account/GetUserAttributes', httpOptions)
      .pipe(
        catchError(this.handleError('getHeroes', {error: true}))
      );
  }

  logoff() {
    const httpOptions = {
      headers: new HttpHeaders({
        'Content-Type': 'application/application/json'
      })
    };

    return this.http.post(this.banistmoURI + 'api/account/Logout', httpOptions)
      .pipe(
        catchError(this.handleError('BanistmoService', {error: true}))
      );
  }

}



