import {HashLocationStrategy, LocationStrategy} from '@angular/common';
import {BrowserModule} from '@angular/platform-browser';
import {NgModule, NO_ERRORS_SCHEMA} from '@angular/core';
import {HttpModule} from '@angular/http';
import {HTTP_INTERCEPTORS, HttpClientModule} from '@angular/common/http';
import {BrowserAnimationsModule} from '@angular/platform-browser/animations';
import {CookieService} from 'ngx-cookie-service';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';

import {AppComponent} from './app.component';
import {SharedService} from './shared/services/shared.service';
import {HttpErrorHandler} from './http-error-handler.service';
import {routing} from './app.routing';
import {AuthInterceptor} from './interceptors/auth-interceptor';
import {DataTableModule} from './shared/components/data-table';
import {Select2Module} from 'ng2-select2';
import {SweetAlertService} from 'angular-sweetalert-service';

import {AreaOperativaService} from './providers/areaOperativa.service';
import {FileService} from './providers/file.service';
import {EmpresaService} from './providers/empresa.service';
import {LoadingIndicatorService} from './providers/loading.service';
import {BanistmoService} from './providers/banistmo.service';
import {RolesService} from './providers/roles.service';
import {UsersService} from './providers/users.service';
import {ModulesService} from './providers/modules.service';
import {CompanyService} from './providers/company.service';


@NgModule({
  schemas: [NO_ERRORS_SCHEMA],
  imports: [
    BrowserModule,
    HttpModule,
    BrowserAnimationsModule,
    routing,
    HttpClientModule,
    FormsModule,
    ReactiveFormsModule,
    DataTableModule,
    Select2Module
  ],
  declarations: [
    AppComponent
  ],
  providers: [
    SharedService,
    BanistmoService,
    RolesService,
    UsersService,
    ModulesService,
    AreaOperativaService,
    LoadingIndicatorService,
    FileService,
    EmpresaService,
    HttpErrorHandler,
    SweetAlertService,
    CookieService,
    CompanyService,
    {
      provide: LocationStrategy,
      useClass: HashLocationStrategy
    },
    {provide: HTTP_INTERCEPTORS, useClass: AuthInterceptor, multi: true}
  ],
  bootstrap: [AppComponent]
})
export class AppModule {
}



