import {Injectable} from '@angular/core';
import {HttpClient, HttpHeaders, HttpParams} from '@angular/common/http';
import {HttpErrorHandler, HandleError} from '../http-error-handler.service';
import {Observable} from 'rxjs/Observable';
import {catchError} from 'rxjs/operators';

@Injectable()
export class UsersService {
  uri = 'http://servicemarket-001-site7.gtempurl.com/api/';
  private handleError: HandleError;


  constructor(private http: HttpClient, httpErrorHandler: HttpErrorHandler) {
    this.handleError = httpErrorHandler.createHandleError('UsersService');
  }


  getById(id: string): Observable<any> {

    const httpOptions = {
      headers: new HttpHeaders({
        'Content-Type': 'application/json'
      })
    };
    return this.http.get(this.uri + 'user/' + id, httpOptions)
      .pipe(
        catchError(this.handleError('getById', {error: true}))
      );
  }

  getByRole(id: string): Observable<any> {

    const httpOptions = {
      headers: new HttpHeaders({
        'Content-Type': 'application/json'
      }),
      params: new HttpParams().set('id', id)
    };
    return this.http.get(this.uri + 'User/UsuariosPorRol', httpOptions)
      .pipe(
        catchError(this.handleError('get', {error: true}))
      );
  }

  get(): Observable<any> {

    const httpOptions = {
      headers: new HttpHeaders({
        'Content-Type': 'application/json'
      })
    };
    return this.http.get(this.uri + 'user', httpOptions)
      .pipe(
        catchError(this.handleError('get', {error: true}))
      );
  }

  save(rol: any): Observable<any> {

    const httpOptions = {
      headers: new HttpHeaders({
        'Content-Type': 'application/json'
      })
    };
    return this.http.post(this.uri + 'user/Create', rol, httpOptions)
      .pipe(
        catchError(this.handleError('save', {error: true}))
      );
  }

  update(user: any): Observable<any> {

    const httpOptions = {
      headers: new HttpHeaders({
        'Content-Type': 'application/json'
      })
    };
    user.Estatus = 2;
    return this.http.put(this.uri + 'user', user, httpOptions)
      .pipe(
        catchError(this.handleError('save', {error: true}))
      );
  }
}



